import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import { Input, DatePicker, Select, Button } from 'antd';
import moment from 'moment';
import styles from './index.module.less';

const { RangePicker } = DatePicker;

// const resultList = [//审核结果可选项
//     {label:"全部",value:0},
//     {label:"通过",value:1},
//     {label:"退回",value:2},
// ];
// const taskTypeList = [//任务类型可选项
//     {label:"全部",value:0},
//     {label:"自主发起",value:2},
//     {label:"部门下发",value:1},
// ];
const dateTypeList = [//日期类型可选项
    {label:"接收日期",value:"reciveDate"},
    // {label:"审核日期",value:"auditDate"},
    {label:"完成日期",value:"completionDate"},
]
const statusList = [//状态可选项
    {label:"全部",value:0},
    {label:"完成",value:4},
    {label:"填报中",value:1},
    {label:"填报审核中",value:2},
    {label:"审核退回",value:3},
    {label:"填报超时",value:5},
    {label:"转办申请",value:6},
    {label:"转办退回",value:8},
]

class FilterBox extends Component {
    constructor(props){
        super(props);
        this.state = {
            formName:"",//表单名称
            planTaskName:"",//计划名称
            // result:0,//审核结果
            // taskType:0,//任务类型
            taskStatus:0,//计划状态
            dateType:"reciveDate",//日期类型
            date:[],//日期
            taskCreOrDeptKeyword:""//下发人/部门
        }
    }
    componentDidMount(){
        document.addEventListener("keydown",this._onkeydown);
    }
    _onkeydown = (e) => {
        if(e.keyCode === 13){
            this.handleSearch();
        }
    }
    componentWillUnmount(){
        document.removeEventListener("keydown",this._onkeydown)
    }
    //输入框修改事件
    handleChangeInput = (e,type) => {
        this.setState({
            [type]:e.target.value
        })
    }
    //下拉框事件
    handleChangeStatus = (value,type) => {
        this.setState({
            [type]:value
        })
    }
    //日期切换
    handleChangeDate = (dates,dateString) => {
        this.setState({
            date:dates
        })
    }
    //搜索
    handleSearch = () => {
        const date = this.state.date.length>0?[moment(this.state.date[0]).format("YYYY-MM-DD HH:MM:SS"),moment(this.state.date[1]).format("YYYY-MM-DD HH:MM:SS")]:[];
        const filterValue = {
            ...this.state,
            [this.state.dateType]:date
        }
        delete filterValue.dateType;
        delete filterValue.date;
        //传给后端所需数据
        this.props.updateFilterValue(filterValue)
    }

    // 重置
    handleReset=()=>{
        this.setState({
            formName:"",//表单名称
            planTaskName:"",//计划名称
            // result:0,//审核结果
            // taskType:0,//任务类型
            taskStatus:0,//计划状态
            dateType:"reciveDate",//日期类型
            date:[],//日期
            taskCreOrDeptKeyword:""//下发人/部门
        },() => {
            this.handleSearch();
        })
        
    }

    render() {
        return (
            <div className={styles.filterBox}>
               <div className={styles.filterItem}>
                    表单名称：
                    <Input style={{width:220}} placeholder="请输入表单名称" value={this.state.formName} onChange={(e) => {this.handleChangeInput(e,"formName")}}/>
                </div>
                <div className={styles.filterItem}>
                    计划名称：
                    <Input style={{width:220}} placeholder="请输入计划名称" value={this.state.planTaskName} onChange={(e) => {this.handleChangeInput(e,"planTaskName")}}/>
                </div>
                {/* <div className={styles.filterItem}>
                    审核结果：
                    <Select style={{width:120}} value={this.state.result} onChange={(val) => this.handleChangeStatus(val,"result")}>
                        {resultList.map((item) => (<Select.Option key={item.value} value={item.value}>{item.label}</Select.Option>))}
                    </Select>
                </div> */}
                {/* <div className={styles.filterItem}>
                    任务类型：
                    <Select style={{width:120}} value={this.state.taskType} onChange={(val) => this.handleChangeStatus(val,"taskType")}>
                        {taskTypeList.map((item) => (<Select.Option key={item.value} value={item.value}>{item.label}</Select.Option>))}
                    </Select>
                </div> */}
                <div className={styles.filterItem}>
                    任务状态：
                    <Select style={{width:120}} value={this.state.taskStatus} onChange={(val) => this.handleChangeStatus(val,"taskStatus")}>
                        {statusList.map((item) => (<Select.Option key={item.value} value={item.value}>{item.label}</Select.Option>))}
                    </Select>
                </div>
                <div className={styles.filterItem}>
                    <Select style={{width:120}} value={this.state.dateType} onChange={(val) => this.handleChangeStatus(val,"dateType")}>
                        {dateTypeList.map((item) => (<Select.Option key={item.value} value={item.value}>{item.label}</Select.Option>))}
                    </Select>
                    <RangePicker style={{width:320}} value={this.state.date} format={"YYYY/MM/DD"} onChange={this.handleChangeDate}/>
                </div>
                <div className={styles.filterItem}>
                    下发人/部门：
                    <Input style={{width:220}} placeholder="请输入关键字查询" value={this.state.taskCreOrDeptKeyword} onChange={(e) => {this.handleChangeInput(e,"taskCreOrDeptKeyword")}}/>
                </div>
                <div className={styles.filterItem}>
                    <Button type='primary' ghost icon="search" style={{marginRight:12}} onClick={this.handleSearch}>搜索</Button>
                    <Button icon="sync" onClick={this.handleReset}>重置</Button>
                </div>
            </div>
        )
    }
}

export default withRouter(FilterBox)