import React, { Component } from 'react';
import { message, Table, Modal, Divider, Button, Tabs, Tooltip, Popconfirm } from 'antd';
import { withRouter } from 'react-router-dom';
import { postAction,getAction } from '@/axios';
import {hasButtonPermission} from '@/util'
import FilterBox from './filterBox';
import ArrachmentTable from './attchmentTable';
import TaskDetail from './taskDetail';
import PreviewDoc from '@/pages/common/PreviewDoc';
import PlanDetailModal from './planDetailModal';
import TransferTaskModal from './transferTask';
import styles from './index.module.less';

const { confirm } = Modal;

const statusList = [//状态可选项
    // {label:"全部",value:0,color:""},
    { label: "完成", value: 4, color: "#52C41A" },
    { label: "填报中", value: 1, color: "#3F54A2" },
    { label: "填报审核中", value: 2, color: "#FADB14" },
    { label: "审核退回", value: 3, color: "#0099ff" },
    { label: "填报超时", value: 5, color: "#FADB56" },
]

class MyFillingTask extends Component {
    constructor(props) {
        super(props);
        this.state = {
            pdfRecord:{},//当前预览的记录信息
            filterValue: {},
            showAttachmentModal: false,
            showCheckModal: false,//查看详情弹窗
            currentTaskInfo: {},//当前选择的任务信息
            dataSource: [],
            pagination:{
                total:0,         //总数
                current:1,
                pageSize:10,
                showTotal:(total, range)=> `总共 ${total} 条`,//显示总条数
                showQuickJumper:true,//是否可以快速跳转至某页
                showSizeChanger:false,//页长设置
            },
            columns: [
                {
                    title: "序号",
                    dataIndex: "sortIndex",
                    width: 70,
                    fixed: "left"
                }, {
                    title: "编号",
                    dataIndex: "processCode",
                    width:180,
                    render:(text)=><span title={text}>{text}</span>
                }, {
                    title: "表单名称",
                    dataIndex: "formName",
                    width:280,
                    render:(text)=><span title={text}>{text}</span>

                }, {
                    title: "所属填报计划",
                    width:160,
                    dataIndex: "planTaskName",
                    render:(text,record) => {
                    return <span className="operation-link" title={text} onClick={()=>{this.openPlanModal(record)}}>{text}</span>
                    }
                },{
                    title: "接收时间",
                    width:160,
                    dataIndex: "taskStartTime",
                }, {
                    title: "任务类型",
                    width:120,
                    dataIndex: "taskTypeName",
                    render: (text) => {
                        return text
                    }
                }, {
                    title: "下发人",
                    width:120,
                    dataIndex: "taskCreateName",
                }, {
                    title: "下发部门",
                    width:140,
                    dataIndex: "creatorDepartName",
                }, {
                    title: "完成时间",
                    width:160,
                    dataIndex: "taskEndTime",
                }, {
                    title: "状态",
                    dataIndex: "taskStatus",
                    width: 130,
                    render: (text,record) => {
                        let status = null;
                        if(record.transmitStatus){
                            if(record.transmitStatus===6){
                                status = { label: "转办申请中", value: 6, color: "#FADB56" }
                            }else{
                                status = { label: "转办退回", value: 8, color: "#FADB56" }
                            }
                        }else{
                            status = statusList.find(item => item.value === text);
                        }
                        return status ? <React.Fragment><i style={{ width: 8, height: 8, display: "inline-block", borderRadius: "50%", marginRight: 5, background: status.color }}></i>{status.label}</React.Fragment> : "未知状态"
                    }
                }, {
                    title: "操作",
                    fixed: "right",
                    width: 180,
                    render: (text, record) => {
                        if(record.transmitStatus){
                            if(record.transmitStatus===6){//转办申请中
                                let curUserId = JSON.parse(window.localStorage.getItem("leftMenuList")).userId;
                                if(record.userId===curUserId){//如果是转办人则有撤回按钮
                                    return <React.Fragment>
                                        {
                                            hasButtonPermission("myFillingTask002") &&
                                            <React.Fragment>
                                                <span className="operation-link" onClick={() => { this.previewTask(record) }}>预览打印</span>
                                                <Divider type="vertical" />
                                            </React.Fragment>
                                        }
                                        {
                                            hasButtonPermission("myFillingTask006") &&
                                            <Popconfirm
                                                title="确定撤回该转办申请吗？"
                                                onConfirm={()=>{this.recallTransfer(record)}}
                                                okText="确定"
                                                cancelText="取消"
                                            >
                                                <span className="operation-link">撤回</span>
                                            </Popconfirm>
                                        }
                                    </React.Fragment>
                                }else{//非转办人，则为受理人，只有处理转办按钮
                                    return <React.Fragment>
                                        {
                                            hasButtonPermission("myFillingTask002") &&
                                            <React.Fragment>
                                                <span className="operation-link" onClick={() => { this.previewTask(record) }}>预览打印</span>
                                                <Divider type="vertical" />
                                            </React.Fragment>
                                        }
                                        {
                                            hasButtonPermission("myFillingTask009") &&
                                            <span className="operation-link" onClick={() => { this.handleTransfer(record)}}>处理转办</span>
                                        }
                                    </React.Fragment>
                                }
                            }else{//转办退回
                                return <React.Fragment>
                                {
                                    hasButtonPermission("myFillingTask002") &&
                                    <React.Fragment>
                                        <span className="operation-link" onClick={() => { this.previewTask(record) }}>预览打印</span>
                                        <Divider type="vertical" />
                                    </React.Fragment>
                                }
                                {
                                    hasButtonPermission("myFillingTask008") && 
                                    <React.Fragment>
                                        <span className="operation-link" onClick={() => { this.viewTransfer(record) }}>查看转办</span>
                                        <Divider type="vertical" />
                                    </React.Fragment>
                                }
                                {
                                    hasButtonPermission("myFillingTask003") && 
                                    <React.Fragment>
                                        <span className="operation-link" onClick={() => { this.fillTask(record) }}>填报</span>
                                        <Divider type="vertical" />
                                    </React.Fragment>
                                }
                                {
                                    hasButtonPermission("myFillingTask004") && 
                                    <React.Fragment>
                                        <span className="operation-link" onClick={() => { this.checkTask(record) }}>查看</span>
                                        <Divider type="vertical" />
                                    </React.Fragment>
                                }
                                {
                                    hasButtonPermission("myFillingTask007") && 
                                    <span className="operation-link" onClick={() => { this.transferTask(record) }}>转办</span>
                                }
                                </React.Fragment>
                            }
                        }else{
                            switch (record.taskStatus) {
                                case 4://填报完成
                                    return <React.Fragment>
                                        {
                                            hasButtonPermission('myFillingTask002') &&
                                            <React.Fragment>
                                                <span className="operation-link" onClick={() => { this.previewTask(record) }}>预览打印</span>
                                                <Divider type="vertical" />
                                            </React.Fragment>
                                        }
                                        {
                                            hasButtonPermission('myFillingTask004') &&
                                            <span className="operation-link" onClick={() => { this.checkTask(record) }}>查看</span>
                                        }
                                    </React.Fragment>
                                case 1://填报中
                                    return record.taskTypeName === '自主发起' ?
                                        <React.Fragment>
                                            {
                                                hasButtonPermission('myFillingTask002') &&
                                                <React.Fragment>
                                                    <span className="operation-link" onClick={() => { this.previewTask(record) }}>预览打印</span>
                                                    <Divider type="vertical" />
                                                </React.Fragment>
                                            }
                                            {
                                                hasButtonPermission('myFillingTask004') &&
                                                <React.Fragment>
                                                <span className="operation-link" onClick={() => { this.checkTask(record) }}>查看</span>
                                                <Divider type="vertical" />
                                                </React.Fragment>
                                            }
                                            {
                                                hasButtonPermission('myFillingTask003') && 
                                                <React.Fragment>
                                                <span className="operation-link" onClick={() => { this.fillTask(record) }}>填报</span>
                                                <Divider type="vertical" />
                                                </React.Fragment>
                                            }
                                            {
                                                hasButtonPermission('myFillingTask005') && 
                                                <React.Fragment>
                                                <span className="operation-link" onClick={() => { this.deleteTask(record) }}>删除</span>
                                                </React.Fragment>
                                            }
                                        </React.Fragment> :
                                        <React.Fragment>
                                            {
                                                hasButtonPermission('myFillingTask002') &&
                                                <React.Fragment>
                                                    <span className="operation-link" onClick={() => { this.previewTask(record) }}>预览打印</span>
                                                    <Divider type="vertical" />
                                                </React.Fragment>
                                            }
                                            {
                                                hasButtonPermission('myFillingTask004') &&
                                                <React.Fragment>
                                                <span className="operation-link" onClick={() => { this.checkTask(record) }}>查看</span>
                                                <Divider type="vertical" />
                                                </React.Fragment>
                                            }
                                            {
                                                hasButtonPermission('myFillingTask003') && 
                                                <React.Fragment>
                                                    <span className="operation-link" onClick={() => { this.fillTask(record) }}>填报</span>
                                                    <Divider type="vertical" />
                                                </React.Fragment>
                                            }
                                            {
                                                hasButtonPermission("myFillingTask007") && 
                                                <span className="operation-link" onClick={() => { this.transferTask(record) }}>转办</span>
                                            }
                                        </React.Fragment>
                                case 2://审核中
                                    return <React.Fragment>
                                            {
                                                hasButtonPermission('myFillingTask002') &&
                                                <React.Fragment>
                                                    <span className="operation-link" onClick={() => { this.previewTask(record) }}>预览打印</span>
                                                    <Divider type="vertical" />
                                                </React.Fragment>
                                            }
                                            {
                                                hasButtonPermission('myFillingTask004') &&
                                                <React.Fragment>
                                                    <span className="operation-link" onClick={() => { this.checkTask(record) }}>查看</span>
                                                    <Divider type="vertical" />
                                                </React.Fragment>
                                            }
                                            {
                                                hasButtonPermission('myFillingTask006') &&
                                                <Popconfirm
                                                    title="确定撤回该任务吗？"
                                                    onConfirm={()=>{this.recall(record)}}
                                                    okText="确定"
                                                    cancelText="取消"
                                                >
                                                    <span className="operation-link">撤回</span>
                                                </Popconfirm>
                                            }
                                        
                                    </React.Fragment>
                                case 3://审核退回
                                    return record.taskTypeName === '自主发起' ?
                                        <React.Fragment>
                                            {
                                                hasButtonPermission('myFillingTask002') &&
                                                <React.Fragment>
                                                    <span className="operation-link" onClick={() => { this.previewTask(record) }}>预览打印</span>
                                                    <Divider type="vertical" />
                                                </React.Fragment>
                                            }
                                            {
                                                hasButtonPermission('myFillingTask004') &&
                                                <React.Fragment>
                                                <span className="operation-link" onClick={() => { this.checkTask(record) }}>查看</span>
                                                <Divider type="vertical" />
                                                </React.Fragment>
                                            }
                                            {
                                                hasButtonPermission('myFillingTask003') && 
                                                <React.Fragment>
                                                <span className="operation-link" onClick={() => { this.fillTask(record) }}>填报</span>
                                                <Divider type="vertical" />
                                                </React.Fragment>
                                            }
                                            {
                                                hasButtonPermission('myFillingTask005') && 
                                                <span className="operation-link" onClick={() => { this.deleteTask(record) }}>删除</span>
                                            }
                                        </React.Fragment> :
                                        <React.Fragment>
                                            {
                                                hasButtonPermission('myFillingTask002') &&
                                                <React.Fragment>
                                                    <span className="operation-link" onClick={() => { this.previewTask(record) }}>预览打印</span>
                                                    <Divider type="vertical" />
                                                </React.Fragment>
                                            }
                                            {
                                                hasButtonPermission('myFillingTask004') &&
                                                <React.Fragment>
                                                <span className="operation-link" onClick={() => { this.checkTask(record) }}>查看</span>
                                                <Divider type="vertical" />
                                                </React.Fragment>
                                            }
                                            {
                                                hasButtonPermission('myFillingTask003') && 
                                                <React.Fragment>
                                                <span className="operation-link" onClick={() => { this.fillTask(record) }}>填报</span>
                                                <Divider type="vertical" />
                                                </React.Fragment>
                                            }
                                            {
                                                hasButtonPermission("myFillingTask007") && 
                                                <span className="operation-link" onClick={() => { this.transferTask(record) }}>转办</span>
                                            }
                                        </React.Fragment>
                                case 5://审核超时
                                    return <React.Fragment>
                                        {
                                                hasButtonPermission('myFillingTask002') &&
                                                <React.Fragment>
                                                    <span className="operation-link" onClick={() => { this.previewTask(record) }}>预览打印</span>
                                                    <Divider type="vertical" />
                                                </React.Fragment>
                                            }
                                            {
                                                hasButtonPermission('myFillingTask004') &&
                                                <React.Fragment>
                                                <span className="operation-link" onClick={() => { this.checkTask(record) }}>查看</span>
                                                <Divider type="vertical" />
                                                </React.Fragment>
                                            }
                                            {
                                                hasButtonPermission("myFillingTask007") && 
                                                <span className="operation-link" onClick={() => { this.transferTask(record) }}>转办</span>
                                            }
                                    </React.Fragment>
                                default://新建
                                    return null
                            }
                        }
                    }
                }
            ],
            taskType:0,//任务类型：0：全部，1：部门下发，2：自主发起
            curPlanRecord:{},//当前查看计划的信息
            showPlanDetailModal:false,//显示计划详情
            showTransferModal:false,//转办弹窗
            transferType:'1',//弹窗类型:1,申请，2:审核，3:查看
        }
    }
    //撤回转办
    recallTransfer = (record) => {
        let params = {
            formTaskId:record.formTaskId,
            transmitStatus:'9',
            resolveResult:''
        };
        postAction('/ybt/form/task/resolveTransmit',params).then(res=>{
            if(res.success){
                message.success('撤回成功');
                this.getTableData();
            }else{
                message.error(res.msg)
            }
        })
    }
    //处理转办
    handleTransfer = (record) => {
        this.setState({
            currentTaskInfo:record,
            showTransferModal:true,
            transferType:"2"
        })
    }
    //查看转办
    viewTransfer = (record) => {
        this.setState({
            currentTaskInfo:record,
            showTransferModal:true,
            transferType:"3"
        })
    }
    //转办
    transferTask = (record) => {
        this.setState({
            currentTaskInfo:record,
            showTransferModal:true,
            transferType:"1"
        })
    }
    //关闭转办弹窗
    closeTransferModal = () => {
        this.setState({
            showTransferModal:false,
            transferType:"1"
        })
    }
    //关闭转办弹窗--同时更新列表
    closeAddUpdateList = () => {
        this.setState({
            currentTaskInfo:{},
            showTransferModal:false,
            transferType:"1"
        })
        this.getTableData();
    }
    //预览
    previewTask = (record) => {
        this.setState({
            showPDF:true,
            pdfRecord:record
        });
    }
    //打开计划详情
    openPlanModal = (record) => {
        console.log('000')
        this.setState({
            curPlanRecord:record,
            showPlanDetailModal:true,
        })
    }
    //关闭计划详情弹窗
    closePlanModal = () => {
        this.setState({
            curPlanRecord:{},
            showPlanDetailModal:false,
        })
    }
    //填报
    fillTask = (record) => {
        window.localStorage.setItem("formIdStep2",record.formId)
        this.props.history.push(`/myFillingTask/step2?id=${record.formTaskId}&type=fill`);
    }
    //删除
    deleteTask = (record) => {
        confirm({
            title: "确定删除该任务吗？",
            okText:'确定',
            cancelText:'取消',
            onOk: () => {
                getAction(`/ybt/form/task/deleteFormTaskById`,{
                    formTaskId:record.formTaskId
                }).then(res=>{
                    if(res.success){
                        message.success('操作成功')
                        this.getTableData("reset")
                    }else{
                        message.error(res.obj)
                    }
                })
            },
            onCancel: () => { }
        })
    }
    //查看
    checkTask = (record) => {
        this.setState({ showCheckModal: true, currentTaskInfo: record })
    }
    //审核中----撤回
    recall = (record) => {
        postAction('/ybt/form/task/revokeSubmit',{formTaskId:record.formTaskId}).then(res=>{
            if(res.success){
                message.success('操作成功')
                this.getTableData("reset")
            }else{
                message.error(res.obj)
            }
        })
    }
    //切换tabs
    _changeTabs = (key) => {
        this.setState({
            taskType:key
        },()=>{
            this.getTableData()
        })
    }
    //搜索
    handleSearch = (filterValue) => {
        this.setState({ filterValue }, () => {
            this.getTableData()
        });
    }
    //打开附件弹窗
    openAttachmentModal = (record) => {
        this.setState({ showAttachmentModal: true, currentTaskInfo: record })
    }
    componentDidMount() {
        this.getTableData()
    }
    componentDidUpdate(preProps){
        if (JSON.stringify(preProps) !== JSON.stringify(this.props)) {
            this.setState({
                filterValue: this.props
            })
        }
    }
    handleTableChange=(pagenation)=>{
        this.setState({
            pagination:pagenation
        },()=>{
            this.getTableData("page")
        })
    }
    getTableData = (type) => {
        let params = {...this.state.filterValue,taskType:this.state.taskType}
        // 过滤参数
        if (params.reciveDate && params.reciveDate.length > 0) {
            params.taskStartTimeStart = params.reciveDate[0]
            params.taskStartTimeEnd = params.reciveDate[1]
        }
        if (params.completionDate && params.completionDate.length > 0) {
            params.taskEndTimeStart = params.completionDate[0]
            params.taskEndTimeEnd = params.completionDate[1]
        }
        delete params.reciveDate
        delete params.completionDate
        let current = 1;
        if(type==="page"){
            current=this.state.pagination.current;
        }
        postAction(`/ybt/form/task/getFormTaskList?pageNum=${current}&pageSize=${this.state.pagination.pageSize}`, params).then(res => {
            if (res.success) {
                let dataSource = res.obj.rows.map((item, index) => {
                    item.sortIndex = index + 1
                    return item
                })
                let pagination={
                    ...this.state.pagination,
                    current:res.obj.page,
                    total:res.obj.total
                }
                this.setState({
                    dataSource,
                    pagination
                })
            }
        })
    }

    render() {
        let transferTitle = '转办申请';
        if(this.state.transferType==='2'){
            transferTitle = '转办审核';
        }else if(this.state.transferType==='3'){
            transferTitle = '转办查看';
        }
        return (
            <div className={styles.container}>
                <div className={styles.card}>
                    <div className={styles.cardTitle}>
                        填报任务管理
                        {/* <Link to="/homePage">返回首页</Link> */}
                    </div>
                    <FilterBox updateFilterValue={(filterValue) => { this.handleSearch(filterValue) }} />
                    <div style={{ margin: "-10px 0 10px 0" }}>
                        {
                            hasButtonPermission('myFillingTask001')&&
                            <Tooltip title='此处为自主发起填报表单，若为部门下发任务请在下方表格内点击“填报”按钮进行填报。' placement="right">
                                <Button icon="plus" type="primary" onClick={() => { this.props.history.push("/myFillingTask/step1") }}>我要填报</Button>
                            </Tooltip>
                        }
                    </div>
                    <Tabs defaultActiveKey="0" onChange={this._changeTabs} tabBarStyle={{marginBottom:"0px",background:"#F5FBFB",padding:"0 30px"}} tabBarGutter={10}>
                        <Tabs.TabPane tab="全 部" key="0"></Tabs.TabPane>
                        <Tabs.TabPane tab="自主发起" key="2"></Tabs.TabPane>
                        <Tabs.TabPane tab="部门下发" key="1"></Tabs.TabPane>
                    </Tabs>
                    <div className={styles.table}>
                        <Table
                            rowKey={record => record.formTaskId}
                            columns={this.state.columns}
                            dataSource={this.state.dataSource}
                            scroll={{ x: 2300 }}
                            pagination={this.state.pagination}
                            onChange={this.handleTableChange}
                        />
                    </div>
                </div>
                <Modal
                    visible={this.state.showAttachmentModal}
                    footer={false}
                    onCancel={() => { this.setState({ showAttachmentModal: false }) }}
                    title={`${this.state.currentTaskInfo.formName}的相关附件`}
                    width={1136}
                >
                    <ArrachmentTable currentTaskInfo={this.state.currentTaskInfo} />
                </Modal>
                {
                    this.state.showCheckModal && <Modal
                    visible={this.state.showCheckModal}
                    width={800}
                    footer={false}
                    onCancel={() => { this.setState({ showCheckModal: false }) }}
                    title={"任务详情"}
                    >
                        <TaskDetail currentTaskInfo={this.state.currentTaskInfo} />
                    </Modal>
                }
                <Modal
                    visible={this.state.showPDF}
                    title={<span style={{fontWeight:"bold"}}>预览打印</span>}
                    width={1200}
                    bodyStyle={{padding:0,height:"70vh"}}
                    destroyOnClose={true}
                    footer={false}
                    onCancel={()=>{this.setState({showPDF:false})}}
                >
                    <PreviewDoc id={this.state.pdfRecord.formTaskId} type='formTaskId'/>
                </Modal>
                <Modal
                    visible={this.state.showPlanDetailModal}
                    title={<span style={{fontWeight:"bold"}}>计划详情</span>}
                    width={1200}
                    footer={false}
                    destroyOnClose={true}
                    onCancel={this.closePlanModal}
                >
                    <PlanDetailModal info={this.state.curPlanRecord}/>
                </Modal>
                <Modal
                    visible={this.state.showTransferModal}
                    title={transferTitle}
                    onCancel={this.closeTransferModal}
                    footer={false}
                    width={800}
                    bodyStyle={{height:"500px",padding:'0px'}}
                >
                    <TransferTaskModal type={this.state.transferType} info={this.state.currentTaskInfo} onClose={this.closeTransferModal} onOk={this.closeAddUpdateList}/>
                </Modal>
            </div>
        )
    }
}

export default withRouter(MyFillingTask)