import React, { Component } from 'react';
import {Table,Row,Col} from 'antd';
import { getAction } from '@/axios';

const statusList = [
    {label:"全部",value:0,color:""},
    {label:"计划审核中",value:2,color:"#FADB14"},
    {label:"计划表单填报中",value:4,color:"#3F54A2"},
    {label:"计划审核退回",value:3,color:"#FA541C"},
    {label:"计划超时",value:5,color:"#FA8C16"},
    {label:"完成",value:6,color:"#52C41A"},
    {label:"编辑中",value:1,color:"#52C41A"},
];

export default class PlanDetailModal extends Component {
    constructor(props){
        super(props)
        this.state = {
            planInfo:{
                planTaskName:'计划名称',
                startTime:'2019-11-12',
                endTime:'2019-12-22',
                status:'审核中',
                originator:'发起人',
                depart:'发起部门'
            },
            formList:[
                // {formName:'表单名称',userNum:10,sortIndex:1}
            ],
            attachmentList:[
                // {planAttachmentName:'附件名称',planAttachmentType:'附件类型',planAttachmentId:'001',sortIndex:1}
            ]
        }
    }
    componentDidMount(){
        // console.log(this.props.info)
        //根据信息发送请求
        getAction(`/ybt/form/task/getPlanDetail?planTaskId=${this.props.info.planTaskId}`).then(res=>{
            // console.log(res);
            if(res.success){
                this.setState({
                    planInfo:{
                        planTaskName:res.obj.planTaskInfo.planTaskName,
                        startTime:res.obj.planTaskInfo.taskPlanStartTime,
                        endTime:res.obj.planTaskInfo.taskPlanEndTime,
                        status:res.obj.planTaskInfo.taskStatus,
                        originator:res.obj.planTaskInfo.creatorName,
                        depart:res.obj.planTaskInfo.creatorDepartName
                    },
                    formList:res.obj.formList.map((item,index)=>({...item,sortIndex:index+1}))||[],
                    attachmentList:res.obj.attachments.map((item,index)=>({...item,sortIndex:index+1}))||[]
                })
            }
        })
    }
    formColumns = [
        {
            title:'序号',
            dataIndex:'sortIndex',
            width:'70px'
        },{
            title:'表单名称',
            dataIndex:'formName',
            render:(text)=>(<span title={text}>{text}</span>)
        },{
            title:'填报人数',
            dataIndex:'userNum'
        }
    ]
    attachmentColumns = [
        {
            title:'序号',
            dataIndex:'sortIndex',
            width:'70px'
        },{
            title:'附件名称',
            dataIndex:'planAttachmentName',
            render:(text)=>(<span title={text}>{text}</span>)
        },{
            title:'附件格式',
            dataIndex:'planAttachmentType'
        },{
            title:'操作',
            width:'120px',
            render:(text,record)=>{
            return (<span className='operation-link' onClick={()=>{this.downLoadAttachment(record)}}>下 载</span>)
            }
        }
    ]
    //下载附件
    downLoadAttachment = (record) => {
        window.open(`http://${window.location.host}/ybt/planTask/download/planAttachment?planAttachmentId=${record.planAttachmentId}`)
    }
    render() {
        return (
            <React.Fragment>
                <Row gutter={20}>
                    <Col span={9}>
                        <span style={{display:"inline-block",width:100}}>计划名称：</span>
                        {this.state.planInfo.planTaskName}
                    </Col>
                    <Col span={9}>
                        <span style={{display:"inline-block",width:100}}>填报时间：</span>
                        {this.state.planInfo.startTime} ~ {this.state.planInfo.endTime}
                    </Col>
                    <Col span={6}>
                        <span style={{display:"inline-block",width:100}}>状态：</span>
                        {statusList.find(item => item.value === this.state.planInfo.status)?
                            statusList.find(item => item.value === this.state.planInfo.status).label:"暂无状态"
                        }
                    </Col>
                    <Col span={9}>
                        <span style={{display:"inline-block",width:100}}>发起人：</span>
                        {this.state.planInfo.originator}
                    </Col>
                    <Col span={9}>
                        <span style={{display:"inline-block",width:100}}>发起部门：</span>
                        {this.state.planInfo.depart}
                    </Col>
                </Row>
                <div style={{margin:"20px 0"}}>计划包含表单：</div>
                <Table
                    rowKey='sortIndex'
                    columns={this.formColumns}
                    dataSource={this.state.formList}
                    bordered
                />
                <div style={{margin:"20px 0"}}>计划包含附件：</div>
                <Table
                    rowKey='planAttachmentId'
                    columns={this.attachmentColumns}
                    dataSource={this.state.attachmentList}
                    bordered
                />
            </React.Fragment>
        )
    }
}
