import React,{useState,useEffect} from 'react';
import {Input,Radio,Button, message,Row,Col,AutoComplete} from 'antd';
import {getAction,postAction} from '@/axios';
import styles from './index.module.less';

//函数防抖
let timer = null;
function debounce(func,wait) {
    return function (...args) {
        if(timer) {
            clearTimeout(timer);
        }
        timer = setTimeout(() => {
            func(...args);
        }, wait);
    };
}

function TransferTask({type='1',info={},...props}){
    const formStyle = {label:6,context:16,rowStyle:{padding:'15px 20px'}};
    const [record,setRecord] = useState([]);//转办记录
    const [receiveUser,setReceiveUser] = useState('');//转办接收人
    const [dataSource,setDataSource] = useState([]);//转办接收人可选项
    const [transferReason,setTransferReason] = useState('');
    const [result,setResult] = useState(7);//是否同意
    const [resultReason,setResultReason] = useState('');//审核原因
    const [receiverId,setReceiverId] = useState('');
    const [receiverName,setReceiverName] = useState('');
    const [resaonInfo,setReasonInfo] = useState({});//当前处理的转办信息

    //根据id获取流程记录
    useEffect(()=>{
        getAction('/ybt/form/task/getTransmitLog?formTaskId='+info.formTaskId).then(res=>{
            if(res.success){
                let record = res.obj.map(item=>({
                    creatorName:item.creatorName,
                    createTime:item.createTime,
                    submitReason:item.submitReason,
                    updateTime:item.updateTime,
                    receiverName:item.receiverName,
                    resolveResult:item.resolveResult,
                    transmitStatus:item.transmitStatus,
                    id:item.transmitId
                }))
                setRecord(record);
                setReasonInfo(record[record.length-1]);
            }else{
                message.error(res.msg)
            }
        })
    },[info])
    //联想搜索
    const onSearch = (searchtext)=>{
        //发送请求获取联想数据
        postAction('/ybt/system/setting/user/getSysUserList?pageNum=1&pageSize=0',{keyword:searchtext}).then(res=>{
            if(res.success){
                let dataSource = res.obj.rows?res.obj.rows.map(item=>({
                    name:item.name,
                    userCode:item.userCode,
                    userId:item.userId
                })):[];
                setDataSource(dataSource)
            }
        })
    }
    //联想修改
    const onChange = (value) => {
        setReceiveUser(value)
    }
    const onSelect = (value,option) => {
        console.log(option.props)
        setReceiverId(option.props.receiverid)
        setReceiverName(option.props.receivername)
    }
    //发起转办/处理转办审核
    const handleOk = () => {
        let params = {formTaskId:info.formTaskId};
        let url = '/ybt/form/task/resolveTransmit';
        if(type==='1'){//发起转办
            url='/ybt/form/task/createTransmit';
            params = {
                formTaskId:info.formTaskId,
                receiverId:receiverId,
                receiverName:receiverName,
                transmitStatus:6,
                submitReason:transferReason
            }
        }else{
            console.log(resaonInfo)
            params = {
                formTaskId:info.formTaskId,
                transmitStatus:result,
                resolveResult:resultReason,
                transmitId:resaonInfo.id
            }
        }
        postAction(url,params).then(res=>{
            if(res.success){
                //操作成功--关闭弹窗
                message.success('操作成功')
                props.onOk();
            }else{
                message.error(res.msg)
            }
        })
    }
    //取消
    const handleCancle = () => {
        props.onClose()
    }
    const children = dataSource.map(item=><AutoComplete.Option receiverid={item.userId} receivername={item.name}  key={`${item.userId}-${item.name}`}>{`${item.name}(${item.userCode})`}</AutoComplete.Option>);
    return <div className={styles.transferTaskModal}>
        <div className={styles.formContainer}>
            <Row style={formStyle.rowStyle}>
                <Col span={formStyle.label}>表单名称:</Col>
                <Col span={formStyle.context}>{info.formName}</Col>
            </Row>
            <Row style={formStyle.rowStyle}>
                <Col span={formStyle.label}>所属填报计划:</Col>
                <Col span={formStyle.context}>{info.planTaskName}</Col>
            </Row>
            {
               type==='1' ?
                //转办申请
                <React.Fragment>
                    <Row style={formStyle.rowStyle}>
                        <Col span={formStyle.label}>转办接收人:</Col>
                        <Col span={formStyle.context}>
                            <AutoComplete
                                value={receiveUser}
                                dataSource={children}
                                style={{ width: 200 }}
                                onSearch={debounce(onSearch, 800)}
                                onSelect={onSelect}
                                onChange={onChange}
                                placeholder="请输入姓名或工号联想"
                            />
                        </Col>
                    </Row>
                    <Row style={formStyle.rowStyle}>
                        <Col span={formStyle.label}>转办原因:</Col>
                        <Col span={formStyle.context}>
                            <Input.TextArea value={transferReason} onChange={(e)=>{setTransferReason(e.target.value)}}/>
                        </Col>
                    </Row>
                </React.Fragment>:
                //转办受理/查看
                type==='2'?
                //受理
                <React.Fragment>
                    <Row style={formStyle.rowStyle}>
                        <Col span={formStyle.label}>转办发起人:</Col>
                        <Col span={formStyle.context}>{resaonInfo.creatorName}</Col>
                    </Row>
                    <Row style={formStyle.rowStyle}>
                        <Col span={formStyle.label}>接收/退回:</Col>
                        <Col span={formStyle.context}>
                            <Radio.Group value={result} onChange={(e)=>{setResult(e.target.value)}}>
                                <Radio value={7}>同意</Radio>
                                <Radio value={8}>拒绝</Radio>
                            </Radio.Group>
                        </Col>
                    </Row>
                    <Row style={formStyle.rowStyle}>
                        <Col span={formStyle.label}>接收/退回原因:</Col>
                        <Col span={formStyle.context}>
                            <Input.TextArea value={resultReason} onChange={(e)=>{setResultReason(e.target.value)}}/>
                        </Col>
                    </Row>
                </React.Fragment>:
                //查看
                <React.Fragment>
                    <Row style={formStyle.rowStyle}>
                        <Col span={formStyle.label}>转办发起人:</Col>
                        <Col span={formStyle.context}>{resaonInfo.creatorName}</Col>
                    </Row>
                    <Row style={formStyle.rowStyle}>
                        <Col span={formStyle.label}>接收/退回:</Col>
                        <Col span={formStyle.context}>
                            <Radio.Group value={resaonInfo.transmitStatus}>
                                <Radio value={7}>同意</Radio>
                                <Radio value={8}>拒绝</Radio>
                            </Radio.Group>
                        </Col>
                    </Row>
                    <Row style={formStyle.rowStyle}>
                        <Col span={formStyle.label}>接收/退回原因:</Col>
                        <Col span={formStyle.context}>
                            <Input.TextArea value={resaonInfo.resolveResult}/>
                        </Col>
                    </Row>
                </React.Fragment>
            }
            <p  style={formStyle.rowStyle}>注：转办申请提交后需要接收人同意接受后才能转办成功。若转办接收人拒绝接受，请重新进行转办申请。</p>
            {
                type==='3'?null:<div style={{textAlign:'center'}}>
                    <Button onClick={handleOk} style={{marginRight:'20px'}}>确 定</Button>
                    <Button onClick={handleCancle}>取 消</Button>
                </div>
            }
        </div>
        <div className={styles.content}>
            <p className={styles.title}>任务跟踪：</p>
            <ul className={styles.ul}>
                {
                    record.map(item => {
                        return (
                            <li key={item.id}>
                                <p>发起人：{item.creatorName}</p>
                                <p>发起时间：{item.createTime}</p>
                                <p>发起原因：{item.submitReason}</p>
                                {
                                    item.transmitStatus===9?
                                    <React.Fragment>
                                         <p>处理人：{item.receiverName}</p>
                                        <p>处理时间：{item.updateTime}</p>
                                        <p>处理结果：转办撤回</p>
                                    </React.Fragment>:null
                                }
                                {
                                    (item.transmitStatus===7||item.transmitStatus===8)?
                                    <React.Fragment>
                                        <p>处理人：{item.receiverName}</p>
                                        <p>处理时间：{item.updateTime}</p>
                                        <p>处理结果：{item.transmitStatus===7?"同意":"拒绝"}</p>
                                        <p>原因：{item.resolveResult}</p>
                                    </React.Fragment>:null
                                }
                            </li>
                        )
                    })
                }
            </ul>
        </div>
    </div>
}

export default TransferTask;